@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500&display=swap);
.welcome-wrapper{position:relative;z-index:2;width:100%;height:100%;display:flex;flex-direction:column;align-items:center;justify-content:center}.welcome-title{margin:0;font-family:'Roboto',sans-serif;font-size:24px;margin-bottom:25px}.welcome-input{width:25%}.welcome-input input{width:100%;padding:10px 15px;margin-bottom:25px;text-align:center;-webkit-appearance:none;-moz-appearance:none;appearance:none;border:0;background-color:transparent;font-family:'Roboto',sans-serif;font-size:24px;border-bottom:1px solid #b0b8c1}.welcome-input input:focus{outline:none}.welcome-input input::-webkit-input-placeholder{font-style:italic}.welcome-input input::-moz-placeholder{font-style:italic}.welcome-input input:-ms-input-placeholder{font-style:italic}.welcome-input input:-moz-placeholder{font-style:italic}.welcome-continue button{-webkit-appearance:none;-moz-appearance:none;appearance:none;background-color:white;border:1px solid #b0b8c1;font-family:'Roboto',sans-serif;font-size:16px;font-weight:600;font-size:16px;color:purple;padding:10px 15px;border-radius:20px;box-shadow:0px 1px 12px -5px #9c9c9c;-webkit-transition:box-shadow .15s ease-in-out;transition:box-shadow .15s ease-in-out;cursor:pointer}.welcome-continue button:focus{outline:none}.welcome-continue button:hover{box-shadow:0px 1px 12px -2px #9c9c9c}

.timer{font-size:26px;color:mediumaquamarine}

.menu{flex:0 1}.menu-wrapper{display:flex;align-items:flex-start;justify-content:space-between}.menu-subtitle{font-size:15px;color:gray;letter-spacing:0.1em}

.dnd-block{position:relative;width:250px;height:250px;border-radius:5px;background-color:white;display:flex;align-items:center;justify-content:center;box-shadow:0px 0px 20px -3px #adadad8f;cursor:pointer}.dnd-block.animate{-webkit-transition:top .15 ease-in-out, left .15 ease-in-out;transition:top .15 ease-in-out, left .15 ease-in-out}.dnd-block img{width:60%;height:auto}

.modal{position:absolute;background:rgba(0,0,0,0.2);width:100%;height:100%;top:0;left:0;display:flex;align-items:center;justify-content:center}.modal-body{background:white;padding:60px 100px;display:flex;border-radius:8px;align-items:center;justify-content:center;flex-direction:column;position:relative}.modal-body p{margin:0px;margin-top:15px}.modal-body p.restart{position:absolute;bottom:15px;left:15px;color:gray}

.game{width:100%;height:100%;position:relative;padding:45px;display:flex;flex-direction:column}.game-area{flex:1 1;display:flex;align-items:center;flex-direction:column;justify-content:space-around}.game-objects{min-height:250px;width:100%;display:flex;align-items:center;justify-content:space-around;margin-top:40px}.game-zones{width:100%;display:flex;align-items:center;justify-content:space-around;margin-top:40px}

.dnd-dropzone{width:250px;height:250px;border-radius:5px;border:2px dashed mediumaquamarine;display:flex;align-items:center;justify-content:center}

html,body,#root{height:100%;min-height:100%}*{box-sizing:border-box}body{margin:0;height:100%;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.decorations{position:absolute;width:100%;height:100%;z-index:-1;background:-webkit-gradient(linear, left bottom, left top, from(rgba(167,189,214,0.411765)), color-stop(110%, transparent));background:linear-gradient(to top, rgba(167,189,214,0.411765), transparent 110%);overflow:hidden}.decorations:after,.decorations:before{content:"";width:300px;height:300px;border-radius:100%;background-color:#ffffff2e;position:absolute;z-index:0}.decorations:after{top:75%;left:25%}.decorations:before{top:40%;left:40%}

